import React, { Component } from 'react';

class About extends Component {
  render() {

    if(this.props.data){
      var name = this.props.data.name;
      var profilepic= "images/"+this.props.data.image;
      var bio = this.props.data.bio;
      var city = this.props.data.address.city;
      var street = this.props.data.address.street;
      var state = this.props.data.address.state;
      var zip = this.props.data.address.zip;
      var phone= this.props.data.phone;
      var email = this.props.data.email;
      var resumeDownload = this.props.data.resumedownload;
    }

    return (
      <section id="about">
      <div className="row">
         <div className="three columns">
            <img className="profile-pic"  src={profilepic} alt="Harishanth Tharmalingam" />
         </div>
         <div className="nine columns main-col">
         <h2>A propos de moi</h2>
            <p>{bio}</p>
            <div className="row">
               <div className="columns contact-details">
                  <h2>Détails</h2>
                  <p className="address">
						   <span>{name}</span><br />
                     <span>{street}</span><br />
                     <span>{zip}, {city}</span><br />
						   {state}<br />
						   <span>{phone}</span><br />
                         <span><a target="_blank" rel="noopener noreferrer" href="mailto:contact@harishanth-tharmalingam.com">{email}</a></span>
					   </p>
               </div>
               <div className="columns download">
                  <p>
                     <a href={resumeDownload} className="button" target="../../public/CV Harishanth THARMALINGAM.pdf"><i className="fa fa-download"></i>Télécharger CV</a>
                  </p>
               </div>
            </div>
         </div>
      </div>

   </section>
    );
  }
}

export default About;
